import CustomButton from "components/CustomButton";
import CustomDropdown from "components/CustomDropDown";
import LogoutModal from "components/LogoutModal";
import CheckClickOutside from "components/OutsideClick";
import useNotification from "hooks/Notifications";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import { logout, setAuthToken, setToken } from "redux/Actions/Auth";
import { LABEL, ROUTES, SNACK_VARIANT, STRINGS } from "shared/Constants";
import { Images } from "shared/Images";
import { getDocsUrl } from "shared/Utilities";
import "./style.scss";

function Header({ logo, headerData = [] }) {
    const dispatch = useDispatch();
    const [collapseOpen, setCollapseOpen] = useState(false);
    const [logoutVisible, setLogoutVisible] = useState(false);
    const profile = useSelector((state) => state.common.profileData);

    const [showProfileDrop, setShowProfileDrop] = useState(false);
    const [msg, sendNotifications] = useNotification();
    const history = useHistory();
    const toggleCollapse = () => {
        document.documentElement.classList.toggle("nav-open");
        setCollapseOpen(!collapseOpen);
    };
    const onLoginClick = () => {
        history.push(ROUTES.LOGIN);
    };
    return (
        <header className="header position-fixed">
            <div className="container d-flex align-items-center justify-content-between">
                <div className="logo" onClick={() => history.push(ROUTES.HOMEPAGE)}>
                    <a>
                        <img
                            src={Images.LogoFront}
                            alt="Locumbay"
                            width={200}
                            className="img-fluid"
                        />
                    </a>
                </div>
                <nav className="navbar">
                    <ul>
                        {headerData.map((headerItem) => (
                            <li onClick={() => history.push(headerItem?.routes)}>
                                {headerItem?.title}
                            </li>
                        ))}
                    </ul>
                </nav>

                <div className="d-flex justify-content-end align-items-center header_btns">
                    {!Object.keys(profile).length ? (
                        <div className="ls_btn">
                            <a onClick={onLoginClick}>Login</a> /{" "}
                            <a
                                onClick={() => {
                                    history.push(ROUTES.SIGNUP);
                                }}
                            >
                                Register
                            </a>
                        </div>
                    ) : (
                        <div
                            className="d-flex align-items-center userProfile position-relative"
                            onClick={() => {
                                setShowProfileDrop(true);
                            }}
                            cursor="pointer"
                        >
                            <div className="avtar_info me-3" cursor="pointer">
                                <span>{profile?.first_name || profile?.company_name}</span>
                            </div>
                            <figure className="avtar avtar50 mb-0" cursor="pointer">
                                <img
                                    src={getDocsUrl(profile?.photo) || Images.DefaultPic}
                                    alt="Profile"
                                />
                            </figure>

                            <CheckClickOutside
                                onClick={() => {
                                    setShowProfileDrop(false);
                                }}
                            >
                                <CustomDropdown show={showProfileDrop}>
                                    <div className="user-body">
                                        {[
                                            {
                                                title: LABEL.MY_PROFILE,
                                                image:
                                                    getDocsUrl(profile?.photo) || Images.DefaultPic,
                                                onClick: () => {
                                                    history.push(ROUTES.PROFILE);
                                                },
                                            },
                                            {
                                                title: LABEL.LOGOUT,
                                                image: Images.logout,
                                                onClick: () => {
                                                    setLogoutVisible(true);
                                                },
                                            },
                                        ].map((i, idx) => {
                                            return (
                                                <div
                                                    key={idx}
                                                    className="dropDown-list"
                                                    onClick={() => {
                                                        i.onClick();
                                                        setShowProfileDrop((prev) => !prev);
                                                    }}
                                                >
                                                    <em>
                                                        <img
                                                            src={i?.image}
                                                            className="img-fluid"
                                                            alt="icon"
                                                            width={20}
                                                        />
                                                    </em>
                                                    <span>{i.title} </span>
                                                </div>
                                            );
                                        })}
                                    </div>
                                </CustomDropdown>
                            </CheckClickOutside>
                        </div>
                    )}

                    <button className="navbar-toggler" onClick={toggleCollapse}>
                        <span class="bar"></span>
                        <span class="bar"></span>
                        <span class="bar"></span>
                    </button>
                </div>
            </div>
            {logoutVisible && (
                <LogoutModal
                    onLogout={() => {
                        dispatch(
                            logout(
                                (response) => {
                                    console.log(response, "response");
                                    sendNotifications({
                                        msg: response.data.message,
                                        variant: SNACK_VARIANT.SUCCESS,
                                    });
                                },
                                (error) => {
                                    console.log(error, "error");
                                    sendNotifications({
                                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                                        variant: SNACK_VARIANT.ERROR,
                                    });
                                },
                            ),
                        );
                    }}
                    showLogout={logoutVisible}
                    setShowLogout={setLogoutVisible}
                />
            )}
        </header>
    );
}

export default Header;
