import CustomButton from "components/CustomButton";
import CustomForm from "components/CustomForm";
import CustomTextInput from "components/CustomTextInput";
import useNotification from "hooks/Notifications";
import React, { useRef, useState } from "react";
import { useDispatch } from "react-redux";
import { cvDescriptionUpload, uploadDocuments } from "redux/Actions/Auth";
import { ERROR_MESSAGES, LABEL, SNACK_VARIANT, STRINGS } from "shared/Constants";
import DocumentViewerModal from "components/DocumentViewer";
import { getDocsUrl } from "shared/Utilities";
import { createUploadPayload, DOCUMENT_TYPE_NAME } from "views/authenticated/constants";
import { INPUT_TYPES } from "../../../../shared/Constants";
import { createDescriptionData } from "./constants";
import "./styles.scss";
import EducationDetails from "./widgets/EducationDetails";
import JobHistory from "./widgets/JobHistory";
function UploadCv({ cv_data = {}, setCvData = () => { } }) {
    const dispatch = useDispatch();
    // console.log(cv_data,"cv_data");
    const [uploadedCv, setUploadedCv] = useState(getDocsUrl(cv_data?.cv) || "");
    const [description, setDescription] = useState(cv_data?.personal_description || "");
    const [msg, sendNotifications] = useNotification();
    const [viewDoc, setViewDoc] = useState(false);
    const [modalOpacity, setModalOpacity] = useState(false);
    const inputRef = useRef(null);
    const handleChange = (event) => {
        if (event.target.files[0].size > 2097152) {
            sendNotifications({
                msg: ERROR_MESSAGES.SIZE,
                variant: SNACK_VARIANT.ERROR,
            });
        } else {
            setUploadedCv(event.target.files[0]);
        }
        event.target.value = null;
    };
    const uploadCv = () => {
        const payload = createUploadPayload(uploadedCv, DOCUMENT_TYPE_NAME.CV, true);
        console.log(uploadedCv, "uploaded cv in cv");
        dispatch(
            uploadDocuments(
                payload,
                (response) => {
                    console.log(response, "response uploaded cv in cv");
                    // if (response.data) {
                    setUploadedCv(response?.data || '');
                    setCvData((prev) => {
                        return {
                            ...prev,
                            cv: response?.data || '',
                        };
                    });
                    // }
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const deleteClick = () => {
        setUploadedCv("");
    };
    const handleDescriptionChange = (event) => {
        setDescription(event.target.value);
    };
    const onPersonalDescriptionClick = () => {
        const payload = createDescriptionData(description);

        dispatch(
            cvDescriptionUpload(
                payload,
                (response) => {
                    uploadCv();
                    setCvData((prev) => {
                        return {
                            ...prev,
                            personal_description: description,
                        };
                    });
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    return (
        <div className="tab_content">
            <div className="row">
                <div className="col-12">
                    <div className={`input_file mb-4 ${uploadedCv ? 'disabled' : ''}`} >
                        <CustomTextInput
                            ref={inputRef}
                            titleText={STRINGS.UPLOAD_CV}
                            type={INPUT_TYPES.FILE}
                            disabled={!!uploadedCv ? true : false}
                            onChange={handleChange}
                            accept="application/pdf,application/vnd.ms-excel"
                        />
                        <label className="data_label form-control" data-label="Choose File">
                            Choose File
                        </label>
                        <span className="error">{STRINGS.ACCEPTED}</span>
                    </div>
                </div>
                {uploadedCv && (
                    <div className="col-12">
                        <label className="mb-2 d-block">{STRINGS.CV_UPLOADED}</label>
                        <div className="upload_doc mb-4">
                            <span
                                onClick={() => {
                                    setModalOpacity(true);
                                    setViewDoc(true);
                                }}
                            >
                                {"CV.pdf"}
                            </span>
                            <button className="btn-danger" onClick={deleteClick}>
                                <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path fill-rule="evenodd" clip-rule="evenodd" d="M1.38109 19.389C1.65055 19.6584 1.91981 19.793 2.32402 19.793C2.72802 19.793 2.99747 19.6582 3.26695 19.389L10.4055 12.2504L17.5441 19.389C17.8136 19.6584 18.2176 19.793 18.4871 19.793C18.7565 19.793 19.1605 19.6582 19.43 19.389C19.9687 18.8503 19.9687 18.0421 19.43 17.5034L12.2914 10.3648L19.43 3.22618C19.9687 2.68746 19.9687 1.87927 19.43 1.34056C18.8913 0.801845 18.0831 0.801845 17.5444 1.34056L10.4058 8.47915L3.26719 1.34056C2.72848 0.801845 1.92029 0.801845 1.38157 1.34056C0.842861 1.87927 0.842861 2.68746 1.38157 3.22618L8.52016 10.3648L1.38157 17.5034C0.842861 18.0423 0.842861 18.8503 1.38157 19.3892L1.38109 19.389Z" fill="white" />
                                </svg>
                            </button>
                        </div>
                    </div>
                )}
            </div>
            <div className="card mb-4">
                <div className="card-body">
                    <label htmlFor="personal-description" className="mb-2">
                        {LABEL.PERSONAL_DESCRIPTION}
                    </label>
                    <div className="field_white">
                        <textarea
                            id="personal-description"
                            onChange={handleDescriptionChange}
                            value={description}
                            rows="5"
                            className="form-control"
                        ></textarea>
                    </div>
                </div>
            </div>
            <JobHistory jobData={cv_data.job_history} setCvData={setCvData} />
            <EducationDetails educationData={cv_data.edu_history} setCvData={setCvData} />
            <div className="text-center mt-3">
                <CustomButton
                    id={"personal-description"}
                    title={STRINGS.SUBMIT}
                    onClick={onPersonalDescriptionClick}
                    className="btn btn-md btn-primary"
                />
            </div>
            {viewDoc && (
                <DocumentViewerModal
                    viewDoc={viewDoc}
                    modalOpacity={modalOpacity}
                    documentUrl={uploadedCv}
                    setViewDoc={setViewDoc}
                    onLoadSuccess={() => {
                        setModalOpacity(false);
                    }}
                />
            )}
        </div>
    );
}

export default UploadCv;
