export const login = (payload, success = () => {}, fail = () => {}) => {
    return {
        type: LOGIN,
        payload,
        success,
        fail,
    };
};
export const deleteEducationDetails = (payload, success = () => {}, fail = () => {}) => {
    return {
        type: DELETE_EDUCATION_DETAILS,
        payload,
        success,
        fail,
    };
};
export const setAuthToken = (payload) => {
    return {
        type: SET_AUTH_TOKEN,
        token: payload,
    };
};
export const setToken = (payload) => {
    return {
        type: SET_TOKEN,
        token: payload,
    };
};
export const setProfileData = (payload) => {
    return {
        type: SET_DATA,
        profileData: {...payload},
    };
};
export const clearProfileData = () => {
    return {
        type: CLEAR_DATA,
    };
};
export const fromVerificationCode = (payload) => {
    return {
        type: FROM_VERIFICATION_CODE,
        value: payload,
    };
};

