import CustomButton from "components/CustomButton";
import CustomForm from "components/CustomForm";
import useNotification from "hooks/Notifications";
import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { deleteEducationDetails, deleteJobDetails, saveEducationData, saveJobHistoryData } from "redux/Actions/Auth";
import { ERROR_MESSAGES, INPUT_TYPES, LABEL, SNACK_VARIANT, STRINGS } from "shared/Constants";
import { Images } from "shared/Images";
import {
    addEducationDetails,
    addJob,
    checkAndDelete,
    checkAndUpdate,
    createDefaultData,
    createEducationDefaultData,
    createEducationHistory,
    createEducationList,
    createItemData,
    createJobHistoryData,
    createJobHistoryList,
} from "../../constants";

function EducationDetails({ educationData = [] ,setCvData = () =>{}}) {
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification();
    const [educationdetailsData, setEducationdetailsData] = useState([
        {
            type: INPUT_TYPES.TEXT,
            registerName: "institution-name0",
            labelName: LABEL.INSTITUTION_NAME,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.INSTITUTION_NAME,
                pattern: ERROR_MESSAGES.VALID_INSTITUTION_NAME,
            },
        },
        {
            type: INPUT_TYPES.TEXT,
            registerName: "qualification0",
            labelName: LABEL.QUALIFICATIONS,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.QUALIFICATIONS,
                pattern: ERROR_MESSAGES.VALID_QUALIFICATIONS,
            },
        },
        {
            type: INPUT_TYPES.DATE,
            registerName: "start-date0",
            labelName: LABEL.START_DATE,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.DATE,
                pattern: ERROR_MESSAGES.VALID_DATE,
            },
        },
        {
            type: INPUT_TYPES.DATE,
            registerName: "end-date0",
            labelName: LABEL.END_DATE,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.DATE,
                pattern: ERROR_MESSAGES.VALID_DATE,
            },
        },
        {
            type: INPUT_TYPES.TEXT,
            registerName: "description0",
            labelName: LABEL.DESCRIPITON,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.DESCRIPITON,
                pattern: ERROR_MESSAGES.VALID_DESCRIPTION,
            },
        },
    ]);
    const [educationHistoryList, setEducationHistoryList] = useState([
        ...createEducationList(educationData.length, educationData)
    ]);
    const [historyData, setHistoryData] = useState(createEducationDefaultData(educationData));
    const handleAddEducation = () => {
        setEducationHistoryList((prev) => [
            ...prev,
            addEducationDetails(educationHistoryList.length),
        ]);
    };
    const onSubmit = (data,event) => {
        const index = event.target.id.slice(event.target.id.lastIndexOf('-')+1)
        const educationHistoryItem = !!educationHistoryList[index]
            ? educationHistoryList[index][0].id
            : null;
        const payload = createEducationHistory(data, educationHistoryItem);
        dispatch(
            saveEducationData(
                payload,
                (response) => {
                    const tempDefaultData = [...historyData]
                    tempDefaultData[index] = {...data,id:response.data.id}
                    setHistoryData(tempDefaultData)
                    const tempListItem = [...educationHistoryList];
                    tempListItem[index] = createItemData(
                        tempListItem[index],
                        true,
                        response.data.id,
                    );
                    
                    setEducationHistoryList(tempListItem);
                    // now we are going to update the values 
                    setCvData((prev)=>{
                        return {
                            ...prev,
                            edu_history:checkAndUpdate(educationData,response.data)
                        }
                    })
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const onEditClick = (educationHistoryIndex) => {
        const editClickData = createItemData(educationHistoryList[educationHistoryIndex], false);
        const educationList = [...educationHistoryList];
        educationList[educationHistoryIndex] = editClickData;
        setEducationHistoryList(educationList);
    };
    const onDeleteClick = (educationHistoryIndex) => {
        if(historyData[educationHistoryIndex]){
            
            dispatch(
                deleteEducationDetails(
                    { id: educationHistoryList[educationHistoryIndex][0].id },
                    (response) => {
                        // now we are going to update the values 
                        setCvData((prev)=>{
                            return {
                                ...prev,
                                edu_history:checkAndDelete(educationData,educationHistoryList[educationHistoryIndex][0].id)
                            }
                        })
                        educationHistoryList.splice(educationHistoryIndex, 1);
                        historyData.splice(educationHistoryIndex,1);
                        setHistoryData(historyData)
                        setEducationHistoryList(educationHistoryList);
                        sendNotifications({
                            msg: response.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,    
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        }else{
            // we need to splice the list array 
            educationHistoryList.splice(educationHistoryIndex,1);
            historyData.splice(educationHistoryIndex,1);
            setHistoryData([...historyData])
            setEducationHistoryList([...educationHistoryList])
        }
    };
    console.log(educationHistoryList,historyData,"both wants to know");
    return (
        <div className="card mb-4">
            <div className="card-body">
                {educationHistoryList.map((educationHistoryItem, educationHistoryIndex) => {
                    return (
                        <React.Fragment key={educationHistoryIndex}>
                            <h6 className="h6 text-captialize mb-3">{STRINGS.EDUCATIONDETAIL}</h6>
                            <div className="box_clone position-relative mb-4" key={`educationHistory-${educationHistoryIndex}`}>
                                <div className="bg-white p-4 rounded">
                                    {console.log(historyData[educationHistoryIndex])}
                                    <CustomForm
                                        id={`educationHistory-${educationHistoryIndex}`}
                                        onSubmit={onSubmit}
                                        formData={educationHistoryItem}
                                        renderSubmitButton={false}
                                        reCaptcha={false}
                                        defaultValues={historyData[educationHistoryIndex]}
                                    />
                                    <div className="d-flex flex-column group_btn">
                                        <CustomButton
                                            type={"submit"}
                                            form={`educationHistory-${educationHistoryIndex}`}
                                            // title={STRINGS.SAVE}
                                            Icon={Images.SaveIcon}
                                            className="btn btn-xs btn-primary mb-2"
                                        />
                                        <CustomButton
                                            // title={STRINGS.EDIT}
                                            onClick={() => onEditClick(educationHistoryIndex)}
                                            className="btn btn-xs btn-secondary mb-2"
                                            Icon={Images.EditIcon}
                                        />
                                        {educationHistoryList.length - 1 > educationHistoryIndex && (
                                            <CustomButton
                                                // title={STRINGS.DELETE}
                                                className="btn btn-xs btn-danger"
                                                Icon={Images.TrashIcon}
                                                onClick={() => onDeleteClick(educationHistoryIndex)}
                                            />
                                        )}
                                    </div>
                                </div>
                            </div>
                        </React.Fragment>
                    );
                })}
                <div className="text-center">
                    <button className="btn btn-md btn-outline-secondary" onClick={handleAddEducation}>{STRINGS.ADD_EDUCATION}</button>
                </div>
            </div>
        </div>
    );
}

export default EducationDetails;
