//to convert date string to a local date string in specified format
export function getLocalizedDateString(dateString, month = "numeric") {
    if (dateString) {
        const format = "yyyy-MM-ddTHH:mm:ss.SSSZ";
        const date = new Date(dateString);
        if (isNaN(date.getTime())) {
            //throw new Error('Invalid date string');
        }
        const options = { year: "numeric", month, day: "numeric" };
        return date.toLocaleDateString(undefined, options);
    } else {
        return "";
    }
}
export function convertDateTimeToTime(dateString) {
    const date = new Date(dateString);
    const hours = date.getHours();
    const minutes = date.getMinutes();
    const ampm = hours >= 12 ? "PM" : "AM";
    const formattedHours = hours % 12 === 0 ? 12 : hours % 12;
    const formattedMinutes = minutes < 10 ? `0${minutes}` : minutes;
    const formattedTime = `${formattedHours}:${formattedMinutes} ${ampm}`;
    return formattedTime;
}
