export const getTableData = (data = []) => {
    // firstly we need to get the no of documents with document type 4
    const othersArr = [
        {
            id: 5,
            title: SERVICES.OTHER,
            uploadedFile: null,
            docId: null,
            document_type: DOCUMENT_TYPE_NAME.OTHER,
        },
    ];
    const normalDataArr = [
        {
            id: 1,
            title: SERVICES.RIGHT_OF_WORK,
            uploadedFile: null,
            docId: null,
            document_type: DOCUMENT_TYPE_NAME.RIGHT_OF_WORK,
        },
        {
            id: 2,
            title: SERVICES.INSURANCE_DOCUMENT,
            uploadedFile: null,
            docId: null,
            document_type: DOCUMENT_TYPE_NAME.INSURANCE,
        },
        {
            id: 3,
            title: SERVICES.EDUCATION,
            uploadedFile: null,
            docId: null,
            document_type: DOCUMENT_TYPE_NAME.EDUCATION,
        },
        {
            id: 4,
            title: SERVICES.IDENTITY,
            uploadedFile: null,
            docId: null,
            document_type: DOCUMENT_TYPE_NAME.IDENTITY,
        },
    ];
    let othersType = 4;
    let index = 0;
    for (let i in data) {
        if (data[i].document_type == DOCUMENT_TYPE_NAME.OTHER) {
            othersArr[index] = {
                id: othersType,
                title: SERVICES.OTHER,
                uploadedFile: data[i]?.document ?? null,
                docId: data[i]?.id ?? null,
                document_type: DOCUMENT_TYPE_NAME.OTHER,
            };
            index++;
            othersType++;
        } else {
            normalDataArr[DOCUMENT_TYPE_REV[data[i].document_type]] = {
                id: DOCUMENT_TYPE_REV[data[i].document_type],
                title: doc_title[data[i].document_type],
                uploadedFile: data[i]?.document ?? null,
                docId: data[i]?.id ?? null,
                document_type: data[i]?.document_type,
                valid_till: data[i]?.valid_till ?? null,
            };
        }
    }
    return [...normalDataArr, ...othersArr];
};
