import CustomForm from "components/CustomForm";
import { Images } from "shared/Images";
import {
    createRegistrationPayload,
    createUploadPayload,
    DOCUMENT_TYPE,
    DOCUMENT_TYPE_NAME,
    getDefaultValues,
    getInititalValueRegistration,
    REGISTRATION_DETAILS_DATA,
} from "views/authenticated/constants";
import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";

import CustomButton from "components/CustomButton";
import { INPUT_TYPES, LABEL, SNACK_VARIANT, STRINGS } from "shared/Constants";
import "./styles.scss";
import Header from "components/Header";
import { HEADER_DATA } from "views/HomePage/constants";
import Footer from "components/Footer/Footer";
import { setProfileData, updateProfile, uploadDocuments } from "redux/Actions/Auth";
import useNotification from "hooks/Notifications";
import { getDocsUrl, removeUrl } from "shared/Utilities";
import DocumentViewerModal from "components/DocumentViewer";
import CustomCheckBox from "components/CustomCheckBox";

function RegistrationDetails({ userDetails, setUserDetails = () => { } }) {
    console.log(userDetails,"userDetails");
    const imageRef = useRef(null);
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification();
    const [defaultValues, setDefaultValues] = useState(getDefaultValues(userDetails));
    const [emailNotifications, setEmailNotifications] = useState(false);
    const profile = useSelector((state) => state.common.profileData);
    const [phone, setPhone] = useState(userDetails?.phone_number);
    const [image, setImage] = useState(userDetails?.photo || Images.DefaultPic);
    const [country, setCountry] = useState(userDetails?.address_details?.country);
    const [region, setRegion] = useState(userDetails?.address_details?.country);
    const [identityDocuments, setIdentityDocuments] = useState(userDetails.Document);
    const [imageFile, setImageFile] = useState(userDetails?.photo ?? "");
    // const []
   
    const onSubmit = (registrationData,countryName,regionName,phoneNumber,dialCode,document) => {
            const { identity_documents, formData: payload } = createRegistrationPayload(
                registrationData,
                countryName,
                regionName,
                phoneNumber,
                dialCode,
                imageFile,
            );
            setDefaultValues(registrationData);
            dispatch(
                updateProfile(
                    payload,
                    (response) => {
                        setImage(getDocsUrl(response.data.photo))
                        setUserDetails({
                            ...getInititalValueRegistration(
                                registrationData,
                                countryName,
                                regionName,
                                phoneNumber,
                                response.data.photo,
                                document,
                            ),
                        });
                        dispatch(
                            setProfileData({
                                ...profile,
                                first_name: registrationData.first_name,
                                last_name: registrationData.last_name,
                                email: registrationData.email,
                                photo: response.data.photo
                            }),
                        );
                        // if (document) {
                        //     const documentPayload = createUploadPayload(
                        //         document,
                        //         DOCUMENT_TYPE_NAME.IDENTITY,
                        //         true,
                        //     );
                        //     dispatch(
                        //         uploadDocuments(
                        //             documentPayload,
                        //             (response) => {
                        //                 sendNotifications({
                        //                     msg: response.message,
                        //                     variant: SNACK_VARIANT.SUCCESS,
                        //                 });
                        //             },
                        //             (error) => {
                        //                 sendNotifications({
                        //                     msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        //                     variant: SNACK_VARIANT.ERROR,
                        //                 });
                        //             },
                        //         ),
                        //     );
                        // }
                        sendNotifications({
                            msg: response.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
            
    };
    const handleImage = (event) => {
        setImageFile(event.target.files[0]);
        setImage(URL.createObjectURL(event.target.files[0]));
    }
    const initialDefaultValues = () =>{
        setDefaultValues(getDefaultValues(userDetails));
        setPhone(userDetails?.phone_number);
        setImage(getDocsUrl(userDetails?.photo) || Images.DefaultPic);
        setImageFile(userDetails?.photo);
        setCountry(userDetails?.address_detail?.country);
        setRegion(userDetails?.address_detail?.state);
        setIdentityDocuments(userDetails?.Document);
    }    
    useEffect(() => {
        initialDefaultValues();
    }, [userDetails]);
    return (
        <div className="tab_content">
            <div className="profile_pic text-center mb-4 pb-3">
                <div className="avtar avtar180">
                    <img height={"100%"} width={"100%"} src={image} alt="" />
                    <input
                        id="selectImage"
                        hidden
                        type="file"
                        ref={imageRef}
                        accept="image/x-png,image/jpeg"
                        onChange={handleImage}
                    />
                </div>
                <button id="plus" onClick={(e) => imageRef.current.click()} className="upload_file">
                    <img height={"100%"} width={"100%"} src={Images.cameraIcon} />
                </button>
            </div>
            <CustomForm
                id={"registration-details"}
                onSubmit={onSubmit}
                reCaptcha={false}
                formData={REGISTRATION_DETAILS_DATA}
                phone={userDetails?.phone_number}
                renderSubmitButton={false}
                region={userDetails?.address_detail?.state}
                country={userDetails?.address_detail?.country}
                identityDocument={userDetails?.Document}
                setDefaultValues={setDefaultValues}
                defaultValues={{ ...defaultValues }}
            />
            {/* <CustomCheckBox checkBoxData={[{id:"email-notifications",title:STRINGS.EMAIL_NOTIFICATIONS,value:emailNotifications}]} onCheckboxSelect={(e) => {
                setEmailNotifications(e.target.checked)
            }}/> */}
            <div className="text-center mt-3">
                <CustomButton
                    className="btn btn-md btn-primary"
                    type={INPUT_TYPES.SUBMIT}
                    form="registration-details"
                    title={STRINGS.SAVE}
                />
            </div>
            {/* {viewDoc && <DocumentViewerModal viewDoc={viewDoc} documentUrl={documentUrl} setViewDoc={viewDoc}/>} */}
        </div>
    );
}

export default RegistrationDetails;
