import Accordion from "components/Accordion";
import BasicTable from "components/CustomBasicTable";
import Footer from "components/Footer/Footer";
import Header from "components/Header";
import ProfileAttributes from "components/ProfileAttributes";
import ProfileBankDetails from "components/ProfileBankDetails";
import ProfileDescription from "components/ProfileDescription";
import ProfileDetails from "components/ProfileDetails";
import { ROUTES, SNACK_VARIANT, STATUS_TYPES, STRINGS, USER_TYPES } from "shared/Constants";
import React, { useEffect, useState } from "react";
import FunctionalTable from "components/CustomFunctionalTable";
import { Images } from "shared/Images";
import { useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import { getAllDetails } from "../../../redux/Actions/Auth";
import useNotification from "hooks/Notifications";
import {
    bankDetails,
    csInUse,
    educationHistoryDetails,
    jobHistoryDetails,
    profileDetails,
    uploadedDocuments,
} from "../constants";
import { HEADER_DATA } from "views/HomePage/constants";
import { getDocsUrl } from "shared/Utilities";
import DocumentViewerModal from "components/DocumentViewer";
import { getUniqueDocumentTypes } from "views/authentication/constants";

const imageURL =
    "https://images.unsplash.com/photo-1575936123452-b67c3203c357?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxzZWFyY2h8Mnx8aW1hZ2V8ZW58MHx8MHx8&w=1000&q=80";

const tableHeader = ["#", "Name", "Qualification", "Start Date", "End Date", "Description"];

const tableHeaderFunc = ["#", "Document Name", "Uploaded Document", "Status", "Action"];

const handleActionClick = (a, b) => {
    console.log(a, b);
};

function Profile({}) {
    const history = useHistory();
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification();
    const [data, setData] = useState({});
    const [bankData, setBankData] = useState([]);
    const [viewDoc, setViewDoc] = useState(false);
    const [documentUrl, setDocumentUrl] = useState("");
    const [csData, setCsData] = useState([]);
    const [imageURL, setImageURL] = useState("");
    const [modalOpacity, setModalOpacity] = useState(false);
    const [profileData, setProfileData] = useState(profileDetails(data));
    useEffect(() => {
        dispatch(
            getAllDetails(
                (response) => {
                    setImageURL(getDocsUrl(response.data.personal_info.photo));
                    setProfileData(profileDetails(response.data));
                    setData(response.data);
                    setBankData(bankDetails(response.data.invoiceDetails));
                    setCsData(csInUse(response.data.csInUse));
                    sendNotifications({
                        msg: response.data.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    }, []);
    const handleDocumentClick = (a, b) => {
        if (a.document) {
            setDocumentUrl(getDocsUrl(a.document));
            setModalOpacity(true);
            setViewDoc(true);
        }
    };
    return (
        <main className="main">
            <Header headerData={HEADER_DATA} />

            <div className="inner_content view_profile_sec">
                <div className="container">
                    <div className="d-flex align-items-center justify-content-between">
                        <div className="title_h1 mb-4">
                            <h1 className="h6">View Profile</h1>
                        </div>
                        <button
                            className="btn btn-xs btn-primary mb-4"
                            onClick={() => {
                                history.push(ROUTES.MANAGE_PROFILE);
                            }}
                        >
                            <img src={Images.EditIcon} alt="" width={20} />
                        </button>
                    </div>
                    <ProfileDetails
                        data={profileData}
                        imageURL={imageURL}
                        rating={3}
                        verifiedName={data.verified_document}
                        profileCompleteness={data.profile_completeness}
                        totalExp={data.experience}
                    />
                    <ProfileAttributes
                        title={"Computer Systems Familiar With:"}
                        attributes={csData}
                    />
                    <ProfileBankDetails title={"Bank Details"} data={bankData} />
                    <ProfileDescription
                        title={"Descriptions"}
                        description={data?.personal_info?.personal_description ?? ""}
                    />

                    <section className="accordin_sec">
                        <Accordion title={"Education"}>
                            <BasicTable
                                theadData={tableHeader}
                                tbodyData={educationHistoryDetails(data.edu_history)}
                            />
                        </Accordion>
                        <Accordion title={"Experience"}>
                            <BasicTable
                                theadData={tableHeader}
                                tbodyData={jobHistoryDetails(data.job_history)}
                            />
                        </Accordion>
                    </section>

                    <div className="card">
                        <div className="card-body">
                            <h2 className="h6 text-captialize">Uploaded Documents</h2>

                            <FunctionalTable
                                theadData={tableHeaderFunc}
                                tbodyData={uploadedDocuments(
                                    getUniqueDocumentTypes(data.documents),
                                )}
                                handleDocumentClick={handleDocumentClick}
                                handleActionClick={handleActionClick}
                                type={USER_TYPES.USER}
                            />
                        </div>
                    </div>
                </div>
            </div>
            {viewDoc && (
                <DocumentViewerModal
                    viewDoc={viewDoc}
                    documentUrl={documentUrl}
                    setViewDoc={setViewDoc}
                    modalOpacity={modalOpacity}
                    onLoadSuccess={() => {
                        setModalOpacity(false);
                    }}
                />
            )}
            <Footer />
        </main>
    );
}

export default Profile;
