import "./styles.scss";

import { Images } from "shared/Images";
import Modal from "react-modal";
import React from "react";

function CustomModal(
    {
        children,
        className = "",
        handleToggle = () => {},
        isOpen = false,
        size = "md",
        style = {},
        clearHandler = () => {},
        modalOpacity = false,
    },
    props,
) {
    return (
        <>
            <Modal
                isOpen={isOpen}
                onRequestClose={handleToggle}
                className={`Modal react_modal_main ${size ? size : ""}`}
                overlayClassName="Overlay"
                ariaHideApp={false}
            >
                <div
                    className={
                        `modal-dialog modal-dialog-centered ${size ? `modal-${size}` : ""}`
                        // className ? className : "modal-dialog modal-dialog-centered"
                    }
                >
                    <div className="modal-content" hidden={modalOpacity ? modalOpacity : false}>
                        <div className="modal-body">
                            <button
                                className="btn-close btn-transparent"
                                onClick={() => {
                                    handleToggle();
                                    clearHandler();
                                }}
                            >
                                <img src={Images.CloseIcon} alt="Close" width="24" />
                            </button>
                            {children}
                        </div>
                    </div>
                </div>
            </Modal>
        </>
    );
}

export default CustomModal;
