export const LOGIN_ERROR_MESSAGES = [
    ERROR_MESSAGES.EMAIL_EMPTY,
    ERROR_MESSAGES.EMAIL_VALID,
    ERROR_MESSAGES.PASSWORD_EMPTY,
    ERROR_MESSAGES.PASSWORD_VALID,
];
export const checkLoginMsg = (emailMsg, passwordMsg) => {
    return LOGIN_ERROR_MESSAGES.includes(emailMsg) && LOGIN_ERROR_MESSAGES.includes(passwordMsg);
};
export const PLACEHOLDER = "Enter here..";
export const REGEX = {
    EMAIL: /^(([^<>()\\.,;:\s@"]+(\.[^<>()\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
    PASSWORD:
        /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[ !"#$%&'()*+,/:;<=>?@[\]^_`{|}~])(?=.{8,})/,
    NAME: /^[\w'\-,.][^0-9_!??/\\+=@#$%&*(){}|~<>;:[\]]{2,}$/,
    NUMBERS: /^[0-9]+$/,
};

