import CustomButton from "components/CustomButton";
import CustomForm from "components/CustomForm";
import useNotification from "hooks/Notifications";
import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { deleteJobDetails, saveJobHistoryData } from "redux/Actions/Auth";
import { ERROR_MESSAGES, INPUT_TYPES, LABEL, SNACK_VARIANT, STRINGS } from "shared/Constants";
import { Images } from "shared/Images";
import './styles.scss';

import {
    addJob,
    checkAndDelete,
    checkAndUpdate,
    createDefaultData,
    createItemData,
    createJobHistoryData,
    createJobHistoryList,
} from "../../constants";

function JobHistory({ jobData = [],setCvData = () =>{} }) {
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification();
    const [jobHistoryData, setJobHistoryData] = useState([
        {
            type: INPUT_TYPES.TEXT,
            registerName: "company_name0",
            labelName: LABEL.COMPANY_NAME,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.COMPANY_NAME,
                pattern: ERROR_MESSAGES.VALID_COMPANY_NAME,
            },
        },
        {
            type: INPUT_TYPES.TEXT,
            registerName: "job_title0",
            labelName: LABEL.JOB_TITLE,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.JOB_TITLE,
                pattern: ERROR_MESSAGES.VALID_JOB_TITLE,
            },
        },
        {
            type: INPUT_TYPES.DATE,
            registerName: "start_date0",
            labelName: LABEL.REGISTRATION_NO,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.DATE,
                pattern: ERROR_MESSAGES.VALID_DATE,
            },
        },
        {
            type: INPUT_TYPES.DATE,
            registerName: "end_date0",
            labelName: LABEL.END_DATE,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.DATE,
                pattern: ERROR_MESSAGES.VALID_DATE,
            },
        },
        {
            type: INPUT_TYPES.TEXT,
            registerName: "description0",
            labelName: LABEL.DESCRIPITON,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.DESCRIPITON,
                pattern: ERROR_MESSAGES.VALID_DESCRIPTION,
            },
        },
    ]);
    const [jobHistoryList, setJobHistoryList] = useState([
        ...createJobHistoryList(jobData.length, jobData),
    ]);
    const [historyData, setHistoryData] = useState(createDefaultData(jobData));
    const handleAddJob = () => {
        setJobHistoryList((prev) => [...prev, addJob(jobHistoryList.length)]);
    };
    const onSubmit = (data,event) => {
        const index = event.target.id.slice(event.target.id.lastIndexOf('-')+1)
        const jobHistoryItem = !!jobHistoryList[index] ? jobHistoryList[index][0].id : null;
        const payload = createJobHistoryData(data, jobHistoryItem);
        dispatch(
            saveJobHistoryData(
                payload,
                (response) => {
                    const tempDefaultData = [...historyData]
                    tempDefaultData[index] = {...data,id:response.data.id}
                    setHistoryData(tempDefaultData)
                    const tempListItem = [...jobHistoryList];
                    tempListItem[index] = createItemData(
                        tempListItem[index],
                        true,
                        response.data.id,
                    );
                    setJobHistoryList(tempListItem);
                    // now we are going to update the values 
                    setCvData((prev)=>{
                        return {
                            ...prev,
                            job_history:checkAndUpdate(jobData,response.data)
                        }
                    })
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const onEditClick = (jobHistoryIndex) => {
        const editClickData = createItemData(jobHistoryList[jobHistoryIndex], false);
        const jobList = [...jobHistoryList];
        jobList[jobHistoryIndex] = editClickData;
        setJobHistoryList(jobList);
    };
    const onDeleteClick = (jobHistoryIndex) => {
        if (historyData[jobHistoryIndex]) { 
        dispatch(
            deleteJobDetails(
                { id: jobHistoryList[jobHistoryIndex][0].id },
                (response) => {
                    jobHistoryList.splice(jobHistoryIndex, 1);
                    historyData.splice(jobHistoryIndex,1);
                    setHistoryData(historyData)
                    setJobHistoryList(jobHistoryList);
                    // now we are going to update the local data
                    setCvData((prev)=>{
                        return {
                            ...prev,
                            job_history:checkAndDelete(jobData,jobHistoryList[jobHistoryIndex][0].id)
                        }
                    })
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
            );
        } else {
             // we need to splice the list array 
             jobHistoryList.splice(jobHistoryIndex,1);
             historyData.splice(jobHistoryIndex,1);
             setHistoryData([...historyData])
             setJobHistoryList([...jobHistoryList])
        }
    };
    return (
        <div className="card mb-4">
            <div className="card-body">
                {jobHistoryList.map((jobHistoryItem, jobHistoryIndex) => {
                    return (
                        <React.Fragment key={jobHistoryIndex}>
                            <h6 className="h6 text-captialize mb-3">{STRINGS.JOB_HISTORY}</h6>
                            <div className="box_clone position-relative mb-4" key={`jobHistory-${jobHistoryIndex}`}>
                                <div className="bg-white p-4 rounded">
                                    <CustomForm
                                        id={`jobHistory-${jobHistoryIndex}`}
                                        onSubmit={onSubmit}
                                        formData={jobHistoryItem}
                                        renderSubmitButton={false}
                                        reCaptcha={false}
                                        defaultValues={historyData[jobHistoryIndex]}
                                    />
                                    <div className="d-flex flex-column group_btn">
                                        <CustomButton
                                            type={"submit"}
                                            form={`jobHistory-${jobHistoryIndex}`}
                                            // title={STRINGS.SAVE}
                                            Icon={Images.SaveIcon}
                                            className="btn btn-xs btn-primary mb-2"
                                        />
                                        <CustomButton
                                            // title={STRINGS.EDIT}
                                            onClick={() => onEditClick(jobHistoryIndex)}
                                            className="btn btn-xs btn-secondary mb-2"
                                            Icon={Images.EditIcon}
                                        />
                                        {jobHistoryList.length-1 > jobHistoryIndex && (
                                            <CustomButton
                                                // title={STRINGS.DELETE}
                                                className="btn btn-xs btn-danger"
                                                Icon={Images.TrashIcon}
                                                onClick={() => onDeleteClick(jobHistoryIndex)}
                                            />
                                        )}
                                    </div>
                                </div>
                            </div>
                        </React.Fragment>
                    );
                })}
                <div className="text-center">
                    <button className="btn btn-md btn-outline-secondary" onClick={handleAddJob}>{STRINGS.ADD_JOB}</button>
                </div>
            </div>
        </div>
    );
}

export default JobHistory;
