import { API_URLS, STATUS_CODES } from "services/Api/Constants";
import {
    FORGOT_PASSWORD,
    fromVerificationCode,
    GET_ACCOUNT_DETAILS,
    GET_CS_IN_USE,
    GET_PROFILE_VERIFICATION,
    GET_REGISTRATION_DETAILS,
    LOGIN,
    LOGOUT,
    OTP_VERIFICATION,
    PROFILE_VERIFICATION,
    REGISTER,
    REGISTRATION_DETAILS,
    RESEND_OTP,
    SAVE_ACCOUNT_DETAILS,
    SAVE_PROFILE_VERIFICATION,
    SAVE_REGISTRATION_DETAILS,
    setAuthToken,
    setToken,
    startLoading,
    stopLoading,
    setProfileData,
    GET_ALL_DETAILS,
    clearProfileData,
    CONFIRM_PASSWORD,
} from "../Actions/Auth";
import { all, put, takeLatest } from "redux-saga/effects";

import { getRequest, postRequest, postRequestNoAuth, updateAuthToken } from "shared/Axios";
import { objectToFormData } from "shared/utils";

// profile verification api for document and available fields updates
function* getProfileVerification({ success = () => {}, fail = () => {} }) {
    try {
        yield put(startLoading());

        const { status, data = {} } = yield getRequest({
            API: API_URLS.PROFILE_VERIFICATION,
        });
        console.log(data, "data at api call");
        success(data);
    } catch (error) {
        console.log(error, "error");
        if (error?.status === STATUS_CODES.UNAUTHORIZED) {
            updateAuthToken(null);
            yield put(clearProfileData());
            yield put(setToken(""));
        }
        fail(error?.data?.message || error?.data?.detail || error?.message);
        // console.log(error);
        // callback(JSON.stringify(error));
    } finally {
        yield put(stopLoading());
    }
}
